<template lang="html">
<div class="">
  <h1>ADMINISTRATION</h1>
</div>
  <div class="overlay" >

    <TabNav :tabs="companyList" :selected="selected"  @selected="setSelected" id="companyNav">
      <Tab v-for="(company,i) in companyList" :key="company" :isSelected="selected === company ">

        <div class="container" v-for="userX in listOfListsOfUsers[i]"  :key="userX._id" >
          <div class="userName">
            {{userX.name}}
          </div>
          <button  class="bb" type="button" name="button" @click="administerVQ(userX)" v-if="!userX.availableTest.vqTest">Vq test</button>
          <button  class="cc" type="button" name="button" @click="administerVQ(userX)" v-if="userX.availableTest.vqTest">Vq test</button>

          <button  class="bb" type="button" name="button" @click="administerTL(userX)" v-if="!userX.availableTest.testLicnosti">Test Licnosti</button>
          <button  class="cc" type="button" name="button" @click="administerTL(userX)" v-if="userX.availableTest.testLicnosti">Test Licnosti</button>

          <button  class="bb" type="button" name="button" @click="administerStavovi(userX)" v-if="!userX.availableTest.stavoviZaposlenih">Stavovi Zaposlenih</button>
          <button  class="cc" type="button" name="button" @click="administerStavovi(userX)" v-if="userX.availableTest.stavoviZaposlenih">Stavovi Zaposlenih</button>

          <button  class="bb" type="button" name="button" @click="administerFB(userX)" v-if="!userX.availableTest.feedbackTest">360 FeedBack</button>
          <button  class="cc" type="button" name="button" @click="administerFB(userX)" v-if="userX.availableTest.feedbackTest">360 FeedBack</button>

          <button  class="bb" type="button" name="button" @click="administerBelbin(userX)" v-if="!userX.availableTest.belbinTest">Belbin Test</button>
          <button  class="cc" type="button" name="button" @click="administerBelbin(userX)" v-if="userX.availableTest.belbinTest">Belbin Test</button>
        </div>
      </Tab>
    </TabNav>

  </div>


</template>

<script>
// @ is an alias to /src
// eslint-disable-next-line no-unused-vars
import {mapActions,mapGetters} from 'vuex';
// eslint-disable-next-line no-unused-vars
import TabNav from '@/components/TabNav.vue'
import Tab from '@/components/Tab.vue'

export default {
    computed: mapGetters(['companys',"users","user"]),
    components:{
      TabNav,
      Tab,
    },
    data(){
      return{
        selected: '',
        companyList:[],
        //each element is a list of users (different lists are for different companies)
        listOfListsOfUsers:[],
      }
    },
    methods: {
      ...mapActions(['getCompanies','getAllUsers','getProfile','administerVQTest','administerTLTest','administerStavoviTest','administerFBTest','administerBelbinTest']),
      setSelected(tab){
        this.selected=tab;
      },
      administerVQ(userAdminister){
        this.administerVQTest(userAdminister._id);
      },
      administerTL(userAdminister){
        this.administerTLTest(userAdminister._id);
      },
      administerStavovi(userAdminister){
        this.administerStavoviTest(userAdminister._id);
      },
      administerFB(userAdminister){
        this.administerFBTest(userAdminister._id);
      },
      administerBelbin(userAdminister){
        this.administerBelbinTest(userAdminister._id);
      },




    },
    async created() {
      await this.getProfile();
      await  this.getCompanies();
      await this.getAllUsers();

      //Fill list of companies (companyList is used by Tabs)
      this.companys.forEach(company => {
        let list = [];
        this.listOfListsOfUsers.push(list);
        this.companyList.push(company.name);
      });
      for (let i = 0; i < this.listOfListsOfUsers.length; i++) {
        let companyName = this.companyList[i];
        for(let j = 0; j< this.users.length; j++){
          if(this.users[j].type === companyName){
            this.listOfListsOfUsers[i].push(this.users[j]);
          }
        }
      }
    },
}
</script>

<style lang="css" scoped>
.container {
  display: flex;
  margin-top: 5px;
}
.bb{
  background-color: #aaffaa;
}
.cc{
  background-color: #ffaaaa;
}
.userName{
  width:200px;
  border-color: #000410;
  border: 1px;
  text-decoration: none;
  border-style: solid;

}

</style>
